@import url(https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,700;1,300&display=swap);
/* Google Fonts -> Ubuntu */

body {
  font-family : 'Ubuntu', sans-serif;
  margin: 0;
  font-size : 1.5em;
  text-align: center;
  background-color: #1b1523;
  color: #fff;
}
.card {
    margin: 5px;
    position: relative;
}

.card img {
    border: 2px solid #fff;
    border-radius: 4px;
}

.card__img {
    display : flex;
    flex-direction: column;
}

/* Card Animations */
.card .card__front {
    transform: rotateY(90deg);
    transition: all ease-in 0.2s;
    position: absolute;
}
.flipped .card__front {
    transform: rotateY(0deg);
    transition-delay: 0.2s;
}

.card .card__back {
    transition: all ease-in 0.2s;
    transition-delay: 0.2s;
}

.flipped .card__back {
    transform: rotateY(90deg);
    transition-delay: 0s;
}
.app {
    max-width: 900px;
    margin: 20px auto;
}

.app__subheader {
    max-width: 900px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

button {
    background :none;
    border: 2px solid #fff;
    padding: 6px 12px;
    border-radius: 4px;
    color: #fff;
    font-weight: bold;
    cursor: pointer;
    font-size: 1em;
}

button:hover {
    color: #fff;
    background: #c23866;
}

.app__cards {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
}

.card__front, .card__back {
    width: 180px;
    height: 180px;
}
